import React, { useContext, useEffect, useState } from "react";
import { Notifications } from "react-push-notification";
import { MainContext } from "../Helpers/Context";
import "./weeklyreport.css";
import { MultiSelect } from "react-multi-select-component";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
const Weekly = () => {
  const [filteredClass, setFilteredClass] = useState([]);
  const [classes, setClasses] = useState([]);
  const [terms, setTerms] = useState([]);
  const [sessions, setSessions] = useState([]);
  const [filteredDate, setFilteredDate] = useState("");
  const [filteredSess, setFilteredSess] = useState("");
  const [filteredTerm, setFilteredTerm] = useState("");
  const [weekly, setWeekly] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  let { url, notifications, loggedSchool } = useContext(MainContext);

  let fetchFilters = () => {
    fetch(`${url}/main/payment/classes`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "include",
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.status) {
          let newCls;
          newCls = data.message[0];
          try {
            setTerms(newCls.terms.split(","));
            setSessions(newCls.sessions.split(","));
            setClasses(JSON.parse(newCls.classes));
            setFilteredSess(newCls.current_session);
            setFilteredTerm(newCls.current_term.toUpperCase());
          } catch {
            notifications.warning("failed to set datas. Try reloading");
          }
        }
      })

      .catch((err) => console.log(err));
  };
  useEffect(() => {
    fetchFilters();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [document.readyState]);

  let focusSelect = () => {
    let tempArr = [];
    if (!classes[0]?.label) {
      classes.map((label) => {
        label = label?.toUpperCase();
        tempArr.push({ label, value: label });
        console.log(label);
        return 0;
      });
      console.log(tempArr);
      setClasses(tempArr);
    }
  };

  let createWeek = (e) => {
    e.preventDefault();
    if (
      filteredClass.length === 0 ||
      filteredTerm === "" ||
      filteredSess === "" ||
      filteredDate === ""
    ) {
      notifications.warning("Enter all important fields");
      return;
    } else {
      setIsLoading(true);
      let value = filteredClass.map((val) => val.value.toLowerCase());
      fetch(`${url}/main/records/weekly`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          classes: value,
          date: filteredDate,
          term: filteredTerm.toLowerCase(),
          sess: filteredSess,
        }),
        credentials: "include",
      })
        .then((res) => res.json())
        .then((data) => {
          setIsLoading(false);
          if (data?.status) {
            let sortWeek = data?.message?.weekly;
            let sortOthers = data?.message?.others;
            let newWeek = [];
            sortWeek?.forEach((week) => {
              let element = sortOthers?.find(
                (other) => week?.class === other?.class
              );
              sortOthers?.forEach((oth, index) => {
                if (element?.class === oth?.class) {
                  sortOthers?.splice(index, 1);
                }
              });

              // console.log(sortOthers);
              // console.log(element);
              newWeek.push({
                ...week,
                overallBus: element?.overallBus,
                onlyBus: false,
              });
            });
            if (sortOthers.length !== 0) {
              sortOthers?.map((other) =>
                newWeek.push({ ...other, onlyBus: true })
              );
            }
            console.log(newWeek);
            setWeekly(newWeek);
          }
        })
        .catch((err) => {
          setIsLoading(false);
          console.log(err);
        });
    }
  };
  return (
    <div className="__weekly">
      <Notifications position="top-right" />
      <h2>Weekly Report</h2>
      <form className="search-container container">
        <label htmlFor="name">Date</label>
        <input
          type="date"
          value={filteredDate}
          onChange={(e) => setFilteredDate(e.target.value)}
          required
          onClick={(e) => focusSelect()}
        />
        {/* <select
          value={filteredClass}
          onChange={(e) => setFilteredClass(e.target.value)}
          className="filter-class" required
        >
          <option value="">Class</option>
          {classes &&
            classes.map((elem) => (
              <option value={elem.toUpperCase()} key={Math.random()}>
                {elem.toUpperCase()}
              </option>
            ))}
        </select> */}
        <MultiSelect
          options={classes}
          value={filteredClass}
          onChange={setFilteredClass}
          labelledBy="Select class"
          ClearIcon
        />
        <select
          value={filteredTerm}
          onChange={(e) => setFilteredTerm(e.target.value)}
          className="filter-term"
        >
          <option value="">Term</option>
          {terms &&
            terms.map((elem) => (
              <option value={elem.toUpperCase()} key={Math.random()}>
                {elem.toUpperCase()}
              </option>
            ))}
        </select>
        <select
          value={filteredSess}
          onChange={(e) => setFilteredSess(e.target.value)}
          className="filter-sess"
        >
          <option value="">Session</option>
          {sessions &&
            sessions.map((elem) => (
              <option value={elem.toUpperCase()} key={Math.random()}>
                {elem.toUpperCase()}
              </option>
            ))}
        </select>
        <div className="btn-cont">
          <button
            className="btn btn-success btn-sm"
            onClick={(e) => createWeek(e)}
          >
            Create
          </button>
        </div>
      </form>
      <div className="weekly-table">
        <table className="table" id="table-to-xls">
          <thead>
            <tr className="colspan">
              <th colSpan={6}>
                <h3>{loggedSchool?.toUpperCase()}</h3>
              </th>
            </tr>
            <tr className="colspan">
              <th colSpan={6}>
                {filteredTerm?.toUpperCase()} FEE WEEKLY RECORD
              </th>
            </tr>
            <tr className="colspan">
              <th colSpan={6}>DATE: {new Date(Date.now())?.toDateString()}</th>
            </tr>
            {filteredClass?.length !== 0 && (
              <tr className="colspan">
                {filteredClass?.length !== 0 && (
                  <th colSpan={6}>
                    {filteredClass?.map((clas, index) => (
                      <span key={Math.random() + clas?.label}>
                        {clas?.label}
                        {filteredClass.length - 1 !== index && ", "}
                      </span>
                    ))}
                  </th>
                )}
              </tr>
            )}
            <tr>
              <th>CLASS</th>
              <th>TUITION</th>
              <th>LESSON</th>
              <th>BUS</th>
              <th>PTA</th>
              <th>TOTAL</th>
            </tr>
          </thead>
          <tbody>
            {isLoading ? (
              <tr className="loading">
                <td colSpan={6} style={{ textAlign: "center" }}>
                  loading...
                </td>
              </tr>
            ) : weekly.length === 0 ? (
              <tr className="loading">
                <td colSpan={6} style={{ textAlign: "center" }}>
                  No records available
                </td>
              </tr>
            ) : (
              weekly.map((week, index) => (
                <tr key={week?.class + new Date() + Math.random()}>
                  <td>{week?.class}</td>
                  <td>{week?.totalTuition?.toLocaleString() || "0"}</td>
                  <td>{week?.totalLesson?.toLocaleString() || "0"}</td>
                  <td>{week?.overallBus?.toLocaleString() || "0"}</td>
                  <td>{week?.totalPTA?.toLocaleString() || "0"}</td>
                  <td>
                    {week?.overallBus
                      ? (
                          parseInt(week?.overTotal) + parseInt(week?.overallBus)
                        )?.toLocaleString()
                      : parseInt(week?.overTotal)?.toLocaleString()}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
        <ReactHTMLTableToExcel
          id="xls-button"
          className="xls-button btn btn-dark btn-sm"
          table="table-to-xls"
          filename={
            "Weekly Report".replace(/ /g, "_") +
            "_" +
            new Date(Date.now()).toLocaleTimeString().replace(/ /g, "_")
          }
          sheet="tablexls"
          buttonText="Download XLS"
        />
      </div>
    </div>
  );
};

export default Weekly;
